<template>
  <!-- <div :v-for="(a, i) in postData.length" :key="a"> -->
  <div>
    <!-- 포스트페이지 -->
    <div v-if="step == 0">
      <Post
        @dblclick="$store.commit('btnLikes')"
        :postData="postData[i]"
        v-for="(a, i) in postData"
        :key="i"
      />
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div
        :class="pushFilterData"
        class="upload-image"
        :style="`background-image:url(${imageUrl})`"
      ></div>
      <div class="filters">
        <FilterBox
          v-for="(filteri, a) in filterData"
          :key="a"
          :imageUrl="imageUrl"
          :filteri="filteri"
          >{{ filterData[a] }}</FilterBox
        >
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div
        :class="pushFilterData"
        class="upload-image"
        :style="`background-image:url(${imageUrl})`"
      ></div>
      <div class="write">
        <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
      </div>
    </div>

    <div v-if="step == 3">
      <MyPage :one="1" />
    </div>
  </div>
</template>

<script>
import Post from './Post';
import MyPage from './MyPage';
import FilterBox from './FilterBox';

export default {
  name: 'Container',
  data() {
    return {
      filterData: [
        'aden',
        '_1977',
        'brannan',
        'brooklyn',
        'clarendon',
        'earlybird',
        'gingham',
        'hudson',
        'inkwell',
        'kelvin',
        'lark',
        'lofi',
        'maven',
        'mayfair',
        'moon',
        'nashville',
        'perpetua',
        'reyes',
        'rise',
        'slumber',
        'stinson',
        'toaster',
        'valencia',
        'walden',
        'willow',
        'xpro2',
      ],
      // pushFilterData: "", // mounted로 받았을 경우.
    };
  },
  props: {
    postData: Array,
    step: Number,
    imageUrl: String,
    pushFilterData: String,
  },
  components: {
    Post,
    MyPage,
    FilterBox,
  },

  // 중요한 데이터는 App.vue에 모아두는 것이 좋기 때문에 Props로 변경
  // mounted() {
  //   this.emitter.on("sendFilterBox", (a) => {
  //     this.pushFilterData = a;
  //     console.log("container==>", this.pushFilterData);
  //   });
  // },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
